import React, { PureComponent, Component } from "react";
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../actions/themeActions';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import '../scss/ThemesSplide.scss'
import "./scss/SlideShow.scss"

class SlideShow extends PureComponent {
  constructor(props) {
    super(props)
    this.myRef = React.createRef();
  }
  state = {
    currentIndex: 0
  }
  componentDidMount() {
    const { heightUpdate } = this.props
    setTimeout(() => heightUpdate(), 1000)
  }
  handleSlideChange = () => {
    const { currentIndex } = this.state
    const nextSlideIndex = currentIndex + 1;
    if (this.myRef && this.myRef.current) {
      if (nextSlideIndex < this.myRef.current.slides.length) this.myRef.current.splide.go(nextSlideIndex)
      else this.myRef.current.splide.go(0)
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) return true
    else if (this.state.currentIndex != prevState.currentIndex) return true;
    else return false
  }


  render() {
    const { postData, completeDataObject, wall, languageSetting, clickToShowPopUp, onClickToCTA } = this.props;
    const delaySetting = wall?.Personalization?.slideDelay || 5;
    const settings = {
      type: 'fade',
      rewind: true,
      speed: 1000,
      // interval: delaySetting * 1000,
      perPage: 1,
      perMove: 1,
      gap: 0,
      autoplay: false,
      pauseOnHover: false,
      padding: 0,
      pagination: false,
      arrows: false,
    };
    const { currentIndex } = this.state;
    return (
      <div className="tb_ss_post_container">
        <Splide hasTrack={false} className="tb_ss_slider_wrappper" options={settings} ref={this.myRef} onActive={(splide, prev, next) => this.setState({ currentIndex: prev.index })}>
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} >
                  <Card ownerId={wall.Wall.owner} autoPlay={currentIndex == index} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} wall={wall} currentIndex={currentIndex} handleSlideChange={this.handleSlideChange} intervalTime={delaySetting} />
                </SplideSlide>
              })}
          </SplideTrack>
        </Splide>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {}
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SlideShow)
