import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import Rating from "../../Elements/Rating/Rating";
import { PostTrimContent } from '../../../../utils'
import Network from "../../Elements/Network/Network";
import { CloudUrl } from "../../../../constants";
import { isEmbed, isSocialEmbed } from "../../../../actions/api";
const trim_content_length = 50;

export default class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      contentHeight: 0,
    };
    this.contentRef = React.createRef();
  }

  componentDidMount() {
    // Set initial height
    this.updateHeight();
    const { currentIndex, intervalTime, itemIndex, itemData, personalization } = this.props
    if (currentIndex == itemIndex && (itemData.type == 2 || itemData.type == 4 || ((itemData.type == 3 || itemData.type == 5) || (itemData.type == 1) && personalization.playFullVideo == 0)) || (itemData.type == 1 && personalization.playFullVideo == 1)) {
      this.intervalId = setTimeout(this.updateIndex, intervalTime * 1000);
    }

    // Add resize event listener
    window.addEventListener('resize', this.updateHeight);
  }
  updateIndex = () => {
    const { handleSlideChange } = this.props
    handleSlideChange()
  }

  componentWillUnmount() {
    // Remove resize event listener
    window.removeEventListener('resize', this.updateHeight);
    clearInterval(this.intervalId);
  }

  updateHeight = () => {
    if (this.contentRef.current) {
      this.setState({ contentHeight: this.contentRef.current.offsetHeight });
    }
  };

  componentWillReceiveProps(nextProps) {
    const { itemIndex, personalization } = this.props
    if (nextProps.currentIndex != itemIndex) {
      clearInterval(this.intervalId)
    }
    if (nextProps.currentIndex == itemIndex && !isSocialEmbed && (nextProps.itemData.type == 2 || nextProps.itemData.type == 4 || ((nextProps.itemData.type == 3 || nextProps.itemData.type == 5 || nextProps.itemData.type == 1) && personalization.playFullVideo == 0)) || (nextProps.itemData.type == 1 && personalization.playFullVideo == 1)) {
      this.intervalId = setTimeout(this.updateIndex, nextProps.intervalTime * 1000);
    }
  }


  render() {
    const { contentHeight } = this.state
    const { itemData, personalization, ThemeRule, wallID, ownerId, wall, autoPlay, intervalTime } = this.props;
    const themeId = personalization.signageTheme

    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) && autoPlay ? true : false;
    const is_imagePost = (itemData.type === 2 || itemData.type === 4) && themeId == 77 ? true : false;
    const rating = (itemData.rating > 0) ? true : false;
    const contentClass = (itemData.rating > 0) ? 'tb_ss_rating_content' : 'tb_ss_content'
    const textDecorate = (itemData.type === 1 && personalization.textDecorate) ? 'tb_ss_text_decoration tb_ss_text_post' : '';
    const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
    const socialIcon = ThemeRule.socialAction ? true : false
    const onlyMedia = (itemData.type != 1 && itemData.hideContent && !socialIcon) ? true : false

    const imageStyle = {
      backgroundImage: `url(${itemData.postFileNew})`
    };
    const backgroundStyle = {
      //background: `linear-gradient(135deg, rgb(16, 96, 57) 0%, rgb(96, 16, 55) 100%)`
    };
    const themeClass = themeId == 74 ? ' tb_mss_t' : themeId == 76 ? ' tb_css_t' : themeId == 77 ? ' tb_zss_t' : ''

    const lineClamp = ((contentHeight - 350) / 30).toFixed()

    
    return (
      <div id={`tb-ss-post-${itemData.id}`} className={`tb_ss_post_wrapper${themeClass}`} tb-network={itemData.network.icon}>
        {themeId == 74 ?
          <div className="tb_ss_blur_bg" style={imageStyle}><div></div></div>
          :
          themeId == 76 ?
            <div className="tb_ss_bg_color" data-network={itemData.network.id} style={backgroundStyle}><div></div></div>
            : null
        }
        <div className={`tb_ss_post_article${themeId == 77 ? ' tb_ss_post_article_full' : ''}${itemData.type === 1 ? ' tb_ss_text_article' : ''}${onlyMedia ? ' tb_ss_media_article' : ''}`}>
          <div className="tb_ss_post_in">
            {mediaType ? <div className={`tb_ss_post_media_wrapp ${is_imagePost ? ` tb_ss_post_animated_media` : ``}`}>
              <Media itemData={{ ...itemData, postFileNew: itemData.filename, loopStop: personalization.playFullVideo == 1 ? true : false }} wallID={wallID} ownerId={ownerId} ThemeRule={ThemeRule} wall={wall} autoPlay={!isEmbed} handleVideoEnded={personalization.playFullVideo == 1 ? this.updateIndex : {}} intervalTime={intervalTime} />
            </div> : ''}
            {!onlyMedia ?
              <div className="tb_ss_contant_wrapper" ref={this.contentRef}>
                <div className="tb_ss_contant_wrap_in">
                  {socialIcon ?
                    <div className="tb_ss_social_">
                      <Network networkClass={'tb_ss_social_ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
                    </div> : ''}

                  {rating ? <div className="tb_ss_rating_ _"><Rating rating={itemData.rating} network={itemData.network} /></div> : ''}
                  {!itemData.hideContent ? <PostContent contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={itemData.font} maxLines={lineClamp} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
                  <AuthorDetails postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
                </div>
              </div>
              : ''}
          </div>
        </div>
      </div>
    );
  }
}
