import React, { Component, memo } from "react";
import { ALT_CONTENT, GET_IMAGE_SIZE } from '../../../../utils'
import ReactPlayer from "react-player"
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { isEmbed, isSocialEmbed, isSocialWall, isTagShop, isTagEmbed } from "../../../../actions/api";
import { reelPlayTimeUpdateData } from "../../../../utils";
import PostBranding from "../Branding/PostBranding";
const mediaReplaceText = (mediaUrl, networkId) => {
    switch (networkId) {
        case 25:
            const soundCloud = String(mediaUrl).replace("https://w.soundcloud.com/player/?url=", "")
            return soundCloud
            break;

        default:
            return mediaUrl
            break;
    }

}
class Video extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            videoLoaded: false,
            imgUrl: this.props.data.postFileNew,
            isVisible: false,

        }
        this.videoRef = React.createRef();
        this.observer = null;
    }
    componentWillMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size == 1 ? null : isTagEmbed ? this.props.size : this.props.size
            })
        } else {
            this.onMediaLoad();
        }
    }

    componentDidMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size == 1 ? null : isTagEmbed ? this.props.size : this.props.size
            })
        } else {
            this.onMediaLoad();
        }
        try {
            this.observer = new IntersectionObserver(
                ([entry]) => {
                    this.setState({ isVisible: entry.isIntersecting });
                },
                {
                    threshold: 0.1, // Video is considered visible if 50% of it is in the viewport
                }
            );

            if (this.videoRef.current) this.observer.observe(this.videoRef.current);
        }
        catch (error) {
            this.setState({ isVisible: true });
        }
    }

    onMediaLoad = async (props) => {
        const { data } = this.props;
        if (data && Object.keys(data).length > 0 && [4, 5, 3].includes(data.type)) {
            if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
                this.setState({
                    paddingBottom: (data.mediaHeight * 100) / data.mediaWidth
                })
            } else {
                try {
                    const { width, height } = await GET_IMAGE_SIZE(data.postFileNew);
                    this.setState({
                        paddingBottom: (height * 100) / width
                    })
                } catch (error) {
                    this.setState({
                        paddingBottom: 100
                    })
                }
            }
        }
    }
    setVideoLoaded = event => {
        this.setState({
            videoLoaded: event
        })
    }

    componentWillUnmount() {
        if (this.observer && this.videoRef.current) {
            this.observer.unobserve(this.videoRef.current);
        }
    }

    render() {
        const { VideoClass, data, wallID, isCover, controls, autoPlay, handleVideoEnded, muted, themeID, intervalTime } = this.props


        const { paddingBottom, videoLoaded, isVisible } = this.state
        const imgSize = {
            small: data.postFileNew,
            large: data.filename,
            orignal: data.file
        }
        const noPaddingThemes = [74, 76, 77]
        const noPadding = noPaddingThemes.includes(themeID)
        const videoStyle = {
            paddingBottom: `${noPadding && isSocialWall ? null : paddingBottom}%`
        };
        const mediaClip = data?.loopStop ? data.mediaUrl : data.mediaClip && String(data.mediaClip).length > 0 ? data.mediaClip : data.mediaUrl
        return <div className={`${VideoClass}_wrap_ mediaHolder${data.id}`} style={videoStyle} ref={this.videoRef}>
            {/* {data?.postBranding ? <PostBranding size={1} /> : null} */}
            <ReactPlayer
            
                // ref={videoRef}
                className={`${VideoClass} video${data.id}`}
                url={mediaReplaceText(mediaClip, data.network.id)}
                data-type="video"
                data-network={data.network.id}
                data-link={data.link}
                data-wall-id={wallID}
                data-item-id={data.id}
                data-feed-id={data.feedId ? data.feedId : ""}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': 'auto',
                            'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`,
                            poster: data.postFileNew
                        }
                    },
                    file: {
                        attributes: {
                            preload: 'metadata',
                            poster: data.postFileNew
                            // onError: (e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)
                        }
                    }
                }}
                loop={isSocialWall && !isSocialEmbed && data?.loopStop ? false : autoPlay ? true : true}
                onError={(e) => {
                    if (isEmbed && (!data.stories || data.stories == 0)) POPUP_VIDEO_RENEW(e, data.id)
                    if (noPadding && handleVideoEnded) setTimeout(() => {
                        handleVideoEnded()
                    }, intervalTime * 1000)
                    //this.setVideoLoaded(false)
                }}
                //(data.stories == 0) ? POPUP_VIDEO_RENEW(e) : null}
                autoPlay={isSocialWall ? isVisible : autoPlay}
                muted={muted}
                volume={autoPlay ? 1 : 0}
                playsinline={true}
                onReady={(e) => { isTagShop ? this.setVideoLoaded(false) : this.setVideoLoaded(true) }}
                playing={isTagShop || isSocialWall ? isVisible : autoPlay}
                height={'100%'}
                width={'100%'}
                controls={controls ? controls : false}
                style={{ backgroundImage: (!videoLoaded) ? `url(${noPadding ? imgSize.large : imgSize.small})` : `url(${noPadding ? imgSize.large : imgSize.small})`, backgroundSize: isCover ? 'cover' : 'contain', backgroundPosition: 'center', backgroundRepeat: 'no-repeat' }}
                onEnded={handleVideoEnded ? handleVideoEnded : null}
            />
        </div>
    }
}
export default memo(Video)


