import { CloudUrl } from "../../../../constants";
import { IS_THEME } from "../../../../utils";
const PostBranding = ({ size }) => {
       const ThemeAlign = id => {
              return IS_THEME(id) && size == 1 ? true : false
       }
       return <div
              ref={(el) => {
                     if (el) {
                            el.style.setProperty('opacity', '0.8', 'important');
                            el.style.setProperty('display', 'flex', 'important');
                            el.style.setProperty('width', `${size == 2 ? '110px' : '75px'}`, 'important');
                            el.style.setProperty('height', `${size == 2 ? '25px' : '18px'}`, 'important');
                            el.style.setProperty('max-width', `${size == 2 ? '110px' : '75px'}`, 'important');
                            el.style.setProperty('max-height', `${size == 2 ? '25px' : '18px'}`, 'important');
                            el.style.setProperty('top', `${ThemeAlign(55) || ThemeAlign(62) || ThemeAlign(61) ? 'auto' : '14px'}`, 'important');
                            el.style.setProperty('left', `${ThemeAlign(62) || ThemeAlign(61) ? 'auto' : '14px'}`, 'important');
                            el.style.setProperty('right', `${ThemeAlign(55) || ThemeAlign(62) || ThemeAlign(61) ? '14px' : 'auto'}`, 'important');
                            el.style.setProperty('bottom', `${ThemeAlign(55) || ThemeAlign(62) || ThemeAlign(61) ? '14px' : 'auto'}`, 'important');
                            el.style.setProperty('margin', '0 auto', 'important');
                            el.style.setProperty('position', 'absolute', 'important');
                            el.style.setProperty('visibility', 'visible', 'important');
                            el.style.setProperty('z-index', '3', 'important');
                            el.style.setProperty('background-image', `url(${CloudUrl}/media/logo/tagshop-light.svg)`, 'important');
                            el.style.setProperty('background-repeat', 'no-repeat', 'important');
                            el.style.setProperty('background-size', 'contain', 'important');
                            el.style.setProperty('clip-path', 'initial', 'important');
                     }
              }}
       >
              <div> </div>
       </div>
}

export default PostBranding;