import React, { PureComponent } from "react";
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import Hotspot from "../Hotspot/Hotspot";
import { ALT_CONTENT, GET_IMAGE_SIZE } from '../../../../utils'
import { CloudUrl } from "../../../../constants";
import { LazyLoadImage, trackWindowScroll  } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";

class Image extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            fixedPaddingBottom: 100,
            imgUrl: this.props.data.postFileNew,
            imgSize: {
                small: props.data.postFileNew,
                large: props.data.filename,
                orignal: props.data.file
            },
            size: {
                height: 100,
                width: 100
            },
            isVisible: false,
        }
        this.videoRef = React.createRef();
        this.observer = null
    }
    componentWillMount() {
        const { data, hotspot, size } = this.props
        if (!size || (data.hotspot && data.ugc_products.length > 0 && hotspot)) {
            this.onMediaLoad();
        } else {
            this.setState({
                paddingBottom: size
            })
        }

    }

    componentDidMount() {
        const { data, hotspot, size } = this.props
        if (!size || (data.hotspot && data.ugc_products.length > 0 && hotspot)) {
            this.onMediaLoad();
        } else {
            this.setState({
                fixedPaddingBottom: size
            })
        }
        try {
            this.observer = new IntersectionObserver(
                ([entry]) => {
                    if (entry.isIntersecting) {
                        this.setState({ isVisible: entry.isIntersecting });
                        this.observer.disconnect();
                    }
                },
                {
                    threshold: 0.1, // Video is considered visible if 50% of it is in the viewport
                }
            );

            if (this.videoRef.current) this.observer.observe(this.videoRef.current);
        }
        catch (error) {
            this.setState({ isVisible: true });
        }

    }
    onSetHeightWithWidth = (width, height) => {
        const imageContainer = document.querySelector(`.track${this.props.data.id}`);
        if (imageContainer) {
            imageContainer.setAttribute("data-height", height);
            imageContainer.setAttribute("data-width", width);
        }
    }

    onMediaLoad = async (props) => {
        const { data } = this.props;
        if (data && Object.keys(data).length > 0 && [2, 4, 5, 3].includes(data.type)) {
            if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
                this.setState({
                    paddingBottom: (data.mediaHeight * 100) / data.mediaWidth,
                    size: {
                        height: parseInt(data.mediaHeight),
                        width: parseInt(data.mediaWidth)
                    }
                })
            } else {
                try {
                    const { width, height } = await GET_IMAGE_SIZE(data.postFileNew);
                    this.setState({
                        paddingBottom: (height * 100) / width,
                        size: {
                            height: parseInt(height),
                            width: parseInt(width)
                        }
                    })
                } catch (error) {
                    this.setState({
                        paddingBottom: 100,
                        size: {
                            height: 100,
                            width: 100
                        }
                    })
                }
            }
        }
    }

    onLoad = event => {
        const { data } = this.props;
        if (event.target.getAttribute("data-load") == 1) this.setState({ imgSize: { ...this.state.imgSize, small: event.target.src } });
        try {
            if ((data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "")) this.onSetHeightWithWidth(data.mediaWidth, data.mediaHeight);
            else this.onSetHeightWithWidth(event.target.naturalWidth ? event.target.naturalWidth : event.target.width, event.target.naturalHeight ? event.target.naturalHeight : event.target.height);
        }
        catch (error) {
            this.onSetHeightWithWidth(event.target.naturalWidth ? event.target.naturalWidth : event.target.width, event.target.naturalHeight ? event.target.naturalHeight : event.target.height);
        }
    }
    componentWillUnmount() {
        if (this.observer) {
            this.observer.disconnect(); // Clean up the observer
        }
    }

    render() {
        const { ImageClass, data, wallID, themeID, ownerId, hotspot } = this.props
        const { imgUrl, paddingBottom, size, fixedPaddingBottom, imgSize, noPadding, isVisible } = this.state

        const imageStyle = {
            paddingBottom: `${this.props.size ? this.props.size == 1 ? null : fixedPaddingBottom : paddingBottom}%`
        };

        const blurImg = {
            backgroundImage: `url(${imgSize.small})`
        };

        if (size.width > size.height) {
            var newWidth = '100%';
            var newHeight = `${(size.height / size.width) * 100}%`;
        } else {
            var newHeight = '100%';
            var newWidth = `${(size.width / size.height) * 100}%`;
        }
        const isHotspot = data.hotspot && data.ugc_products.length > 0 && hotspot ? true : false

        const hotspotImgStyle = { width: isHotspot && this.props.size ? newWidth : null, height: isHotspot && this.props.size ? newHeight : null }

        return <div className={`${ImageClass}_wrap_ mediaHolder${data.id}`} style={imageStyle} ref={this.videoRef}>
            {isHotspot ? <div className="tb_blur_bg_" style={blurImg}></div> : null}
            <div className={`${ImageClass}_wrap_in`} style={hotspotImgStyle}>
                {isHotspot ? <Hotspot product={data.ugc_products} dataItem={data} /> : null}
                
                <LazyLoadImage
                    effect="blur"
                    className={`${ImageClass} track${data.id}`}
                    src={data.postFileNew}
                    //srcSet={`${data.postFileNew} 400w, ${data.file} 1200w`}
                    sizes="(max-width: 700px) 0px, 620px"
                    loading="lazy"
                    placeholderSrc="https://via.placeholder.com/200x300"
                    decoding="async"
                    //data-src={imgUrl}
                    height={300}
                    width={300}
                    data-link={data.link}
                    data-load="0"
                    data-network={data.network.id}
                    data-wall-id={wallID}
                    data-owner-id={ownerId}
                    data-item-id={data.id}
                    data-feed-id={data.feedId ? data.feedId : ""}
                    data-filter-id={data.filterId}
                    theme-id={themeID}
                    onLoad={this.onLoad}
                    onError={!data.stories ? (e) => {
                        e.target.src = `${CloudUrl}/media/error/no-image.svg`
                        e.target.srcset = `${CloudUrl}/media/error/no-image.svg`
                        //POPUP_IMAGE_RENEW_REQUEST(e)
                    } : null}
                    alt={ALT_CONTENT(data.content)}
                />
            </div>
        </div>
    }
}

export default trackWindowScroll(Image)