import { isEmbed } from "../../../../actions/api";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall, autoPlay, handleVideoEnded, intervalTime }) => {
  const isVideo = (itemData.type === 3 || itemData.type === 5) && autoPlay
  return <div className="tb_ss_media_wrap">
    {isVideo ?
      <Video VideoClass="tb_ss_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} isCover={true} controls={false} autoPlay={!isEmbed} muted={true} handleVideoEnded={handleVideoEnded} intervalTime={intervalTime} />
      :
      <Image ImageClass="tb_ss_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={1} ThemeRule={wall.ThemeRule} />
    }
  </div>
}
export default Media